.plan {
	margin-bottom: 166px;
	padding: 0 263px;
	padding-top: 40px;
	background-image: url('../../img/plan/sky.png');
	background-size: 1920px 907px; }

.plan__title {
	font-size: 32px;
	line-height: 44px;
	text-align: center;
	letter-spacing: 0.04em;

	.title--line:after {
		bottom: -5px;
		width: 70px;
		background-size: 70px 10px;
		background-image: url('../../img/plan/line.svg'); } }

.plan__body {
	position: relative;
	padding-top: 132px;
	padding-left: 91px; }

.plan__column {
	position: absolute;
	display: flex;
	align-items: flex-start;

	&:nth-child(2) {
		top: 87px;
		left: 22px; }

	&:nth-child(3) {
		top: 290px;
		left: 489px; }

	&:nth-child(4) {
		top: 507px;
		left: 969px; } }

.plan__column-count {
	margin-right: 41px;
	width: 80px;
	border-radius: 50%;
	font-weight: 700;
	font-size: 40px;
	line-height: 80px;
	text-align: center;
	color: $btnTextColor;
	background: $btnBgAccentColor; }

.plan__column-text {
	font-family: $commonFont;
	font-size: 20px;
	line-height: 23.7px;
	letter-spacing: 0.04em;
	color: $textCommonColor; }

.plan__column-link {
	line-height: 23.7px;
	letter-spacing: 0.04em;
	text-decoration: underline;
	color: $textAdditionalColor; }

.plan__box-container {
	position: relative; }

.plan__img {
	position: absolute;
	top: -5px;
	left: -380px;
	offset-path: path("M1.14,113.46c342-174.75,414.3-148.13,866.23,115.79,347.5,202.94,530.82,218.58,643.95,220.16,129.64,1.81,223.82-45.27,223.82-45.27");
	z-index: 1; }

.cls-1 {
	width: 1304px;
	height: 448px; }

.plan__box-img-back {
	position: absolute;
	top: -5px;
	left: -380px;
	z-index: -1; }
