.main-block {
	margin-bottom: 200px;
	padding-left: 263px;
	display: flex;
	justify-content: space-between;
	width: 100%; }

.main-block__prev {
	font-size: 15px;
	line-height: 25px;
	letter-spacing: 0.08em;
	text-transform: uppercase;
	color: $textLightColor; }

.main-block__title {
	margin-bottom: 40px;
	font-family: $titlesFont;
	font-weight: 700;
	font-size: 55px;
	line-height: 67px; }

.main-block__text {
	margin-bottom: 59px;
	font-size: 18px;
	line-height: 150%;
	letter-spacing: 0.05em;
	color: $textCommonColor; }

.main-block__button {
	padding: 18px 73px;
	background: $btnBgColor;
	box-shadow: 0px 15px 30px $mainBlockShadowColor;

	font-size: 20px;
	line-height: 24px;
	letter-spacing: 0.04em;

	color: $btnTextColor;
	transition: all 0.3s ease 0s;

	&:hover {
		background: darken($btnBgColor, 10%); }

	&:active, &:focus {
		background: lighten($btnBgColor, 10%); } }

.main-block__inner {
	display: flex;
	justify-content: space-between;
	align-items: flex-start; }

.main-block__wrapper {
	padding-top: 329px;
	padding-bottom: 55px; }
