.social {
	padding-left: 44px; }

.social__title {
	font-family: $titlesFont;
	font-size: 20px;
	line-height: 27px;
	letter-spacing: 0.03em;
	margin: 0px 0px 9px 0px;
	color: $textCommonColor; }

.social__list {
	display: flex;
	justify-content: space-between; }

.social__link {
	display: flex;
	justify-content: center;
	align-items: center;
	width: 50px;
	height: 50px;
	border: 1px solid #666;
	border-radius: 10px;
	&:hover {
		background-color: #666; } }
