* {
	padding: 0;
	margin: 0;
	border: 0; }

*,*::before, *:after {
	-moz-box-sizing: border-box;
	-webkit-box-sizing: border-box;
	box-sizing: border-box; }

a:focus,a:active {
	outline: none; }

nav,footer,header,aside {
	display: block; }

html,body {
	height: 100%;
	font-size: 100%;
	width: 1920px;
	line-height: 1;
	font-size: 14px;
	-ms-text-size-adjust: 100%;
	-moz-text-size-adjust: 100%;
	-webkit-text-size-adjust: 100%; }

input,button,textarea {
	font-family: inherit; }

input::-ms-clear {
	display: none; }

a, a:visited {
	text-decoration: none; }

a:hover {
	text-decoration: none; }

ul li {
	list-style: none; }
img {
	vertical-align: top; }

body {
	color: $commonColor;
	background: $bodyColor;
	font-family: $commonFont;
	font-weight: 400; }

h1,h2,h3,h4,h5,h6 {
	margin: 0;
	font-family: $titlesFont;
	font-size: inherit;
	font-weight: 700;
	color: $textTitleColor; }

a {
	font-family: $commonFont;
	color: $mainColor;
	font-weight: 400; }

button {
	font-family: $commonFont;
	font-weight: 700;
	cursor: pointer;
	border-radius: 15px; }

button::-moz-focus-inner {
	padding: 0;
	border: 0; }

/*
 * Hide only visually, but have it available for screenreaders: h5bp.com/v
 */

.visuallyhidden {
	border: 0;
	clip: rect(0 0 0 0);
	height: 1px;
	margin: -1px;
	overflow: hidden;
	padding: 0;
	position: absolute;
	width: 1px; }

/*
 * Extends the .visuallyhidden class to allow the element to be focusable
 * when navigated to via the keyboard: h5bp.com/p
 */

.visuallyhidden.focusable:active, .visuallyhidden.focusable:focus {
	clip: auto;
	height: auto;
	margin: 0;
	overflow: visible;
	position: static;
	width: auto; }

.title--line {
	position: relative; }

.title--line:after {
	content: "";

	position: absolute;
	left: 0;
	bottom: -9px;

	width: 105px;
	height: 10px;

	background-image: url('../../img/features/line.svg');

	background-size: 105px 10px;

 }	// Настройки адаптивной сетки ===============================================================================================================================================================================================================================================================================================

// Минимальная ширина страницы
$minWidth: 320;
// Ширина полотна (макета)
$maxWidth: 2000;
// Ширина ограничивающего контейнера (0 = нет ограничения)
$maxWidthContainer: 1890;
// Общий отступ у контейнера
// (30 = по 15px слева и справа, 0 = нет отступа)
$containerPadding: 30;

// Ширина срабатывания первого брейкпоинта
$containerWidth: $maxWidthContainer + $containerPadding;

// Брейк-поинты
$pc: $containerWidth + "px"; // ПК, ноутбуки, некоторые планшеты в горизонтальном положении
$tablet: 991.98px; // Планшеты, некоторые телефоны в горизонтальном положении
$mobile: 767.98px; // Телефоны L
$mobileSmall: 479.98px; // Телефоны S

// Тип адаптива:
// 1 = отзывчивость (у контейнера нет брейкпоинтов),
// 2 = по брейк-поинтам (контейнер меняет свою ширину по брейк-поинтам)
$responsiveType: 1;

// Ограничивающий контейнер ======================================================================================================================================================================================================================

//(i) Стили будут применяться ко всем классам содержащим *__container

@if ($responsiveType==1) {
	// Отзывчивая
	[class*="__container"] {
		@if ($maxWidthContainer>0) {
			max-width: $maxWidthContainer + px;
			box-sizing: content-box;
			margin: 0 auto; }

		@if ($containerPadding>0) {
			padding: $containerPadding/2 + px; }

		@else {} }
	// По брейк-поинтам
	[class*="__container"] {
		margin: 0 auto;
		@if ($maxWidthContainer>0) {
			max-width: $maxWidthContainer + px;
			box-sizing: content-box; }
		@else {
			@if ($containerPadding>0) {
				padding: 0 $containerPadding/2 + px; } }

		@media (max-width: $pc) {
			max-width: 970px; }

		@media (max-width: $tablet) {
			max-width: 750px; }

		@media (max-width: $mobile) {
			max-width: none;
			@if ($containerPadding>0 and $maxWidthContainer>0) {
				padding: 0 $containerPadding/2 + px; } } } }
